.navWrapper {
    width: 200px;
    right: 40px;
    height: 100vh;
    display: flex;
    position: fixed;
    align-items: flex-end;
    flex-direction: column;
    justify-content: center;
    background: transparent;
    gap: 20px;

    overflow: hidden;

    .linkWrapper {
        width: 30px;
        display: flex;
        align-items: center;
        justify-content: flex-end;
        background: transparent;
        border: none;
        cursor: pointer;
        position: relative;
        transition: all 0.3s ease;

        .paginationWrapper {
            display: flex;
            align-items: center;
            justify-content: flex-end;
            width: 30px;
            height: 30px;
            position: relative;
            transition: width 0.3s ease;

            .circle-linkPagination {
                width: 10px;
                height: 10px;
                background-color: #ffffff5d;
                border-radius: 50%;
                position: absolute;
            }

            .active {
                background-color: #fff;
            }

            .linkName {
                position: absolute;
                width: 100px;
                right: -100%;
                white-space: nowrap;
                align-items: center;
                display: flex;
                height: 100%;
                transition: right 0.35s ease;
                z-index: -1;
                color: #a0a0a0;
            }

            .h4TextAnim {
                color: #fff;
                right: 0;
                z-index: 1;
            }
        }

        &.linkWrapperAnim {
            width: 200px;

            .paginationWrapper {
                width: 100px;
                justify-content: flex-end;
                /* background-color: orange; */
            }
        }
    }
}